@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,200;0,300;0,400;0,600;0,700;1,200;1,300;1,400;1,600;1,700&display=swap");
html {
  font-size: 100%;
  scroll-behavior: smooth;
}
body {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1rem;
}
.section-list-organization input.ant-input,
.preview-section input.ant-input,
.business-uploads input.ant-input,
.section-list-organization .ant-form-item-control-input-content textarea.ant-intput,
.preview-section .ant-form-item-control-input-content textarea.ant-intput,
.business-uploads .ant-form-item-control-input-content textarea.ant-intput,
.section-list-organization .ant-select-selector,
.preview-section .ant-select-selector,
.business-uploads .ant-select-selector,
.section-list-organization .ant-input-affix-wrapper,
.preview-section .ant-input-affix-wrapper,
.business-uploads .ant-input-affix-wrapper {
  background-color: #f9f9f9;
}
.section-list-organization .ant-select:not(.ant-select-customize-input) .ant-select-selector,
.preview-section .ant-select:not(.ant-select-customize-input) .ant-select-selector,
.business-uploads .ant-select:not(.ant-select-customize-input) .ant-select-selector {
  background-color: #f9f9f9;
}
.table-responsive .ant-table-content {
  display: block;
  width: 100%;
  overflow-x: auto;
}
.section-landing .ant-btn-icon-only.ant-btn-lg {
  height: 3.125rem;
  width: 3.125rem;
}
.d-flex {
  display: flex !important;
}
.m-0 {
  margin: 0;
}
.p-0 {
  padding: 0 !important;
}
.font-weight-700 {
  font-weight: 700;
}
.font-weight-500 {
  font-weight: 500;
}
.font-weight-400 {
  font-weight: 400;
}
.ant-form-item .ant-upload {
  width: 100%;
}
section.section-list-organization.ant-input::placeholder,
section.business-uploads.ant-input::placeholder,
section.section-landing.ant-input::placeholder,
section.section-list-organization .ant-select-selection-placeholder,
section.business-uploads .ant-select-selection-placeholder,
section.section-landing .ant-select-selection-placeholder {
  color: #707070 !important;
}
section.section-list-organization .ant-select-selection-placeholder,
section.business-uploads .ant-select-selection-placeholder,
section.section-landing .ant-select-selection-placeholder {
  color: #707070 !important;
}
section.section-list-organization.ant-input,
section.business-uploads.ant-input,
section.section-landing.ant-input,
section.section-list-organization #login_password,
section.business-uploads #login_password,
section.section-landing #login_password,
section.section-list-organization.ant-input-affix-wrapper > input.ant-input,
section.business-uploads.ant-input-affix-wrapper > input.ant-input,
section.section-landing.ant-input-affix-wrapper > input.ant-input {
  background-color: #f9f9f9;
}
.text-capitalize {
  text-transform: capitalize;
}
@media screen and (max-width: 576px) {
  html {
    font-size: 80%;
  }
}
