@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,200;0,300;0,400;0,600;0,700;1,200;1,300;1,400;1,600;1,700&display=swap);

@media screen and (max-width: 682px) {
  .ant-page-header .ant-page-header-heading {
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
@media screen and (max-width: 682px) {
  .ant-page-header .ant-page-header-heading .ant-page-header-heading-left .ant-page-header-heading-title {
    text-align: center;
    white-space: inherit;
  }
}

.sector-funding-chart {
  position: relative;
}
.sector-funding-chart p {
  position: absolute;
  font-weight: 700;
  top: 50%;
  left: 50%;
  text-align: center;
  width: 200px;
  margin-bottom: 0;
  display: inline-block;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.styled-collapse .ant-collapse-item .ant-collapse-header {
  padding: 1.3rem 0;
}
.styled-collapse .ant-collapse-item .ant-collapse-header p {
  margin-bottom: 0;
  width: 90%;
}
.styled-collapse .ant-collapse-icon-position-right > .ant-collapse-item > .ant-collapse-header {
  display: flex;
  align-items: center;
  padding: 1.5rem 1rem 1rem 0;
}
.styled-collapse .ant-collapse > .ant-collapse-item > .ant-collapse-header .ant-collapse-arrow {
  top: 0;
}
.styled-collapse .edit-content-btn {
  margin-top: 15px;
  text-align: right;
}
.ant-collapse-content-box {
  padding-left: 0 !important;
  padding-right: 2rem !important;
}
.ant-collapse-item:nth-child(6),
.ant-collapse-item:nth-child(12) {
  border-bottom: none;
}
.message-header {
  font-size: 30px;
  margin-bottom: 2.5rem;
}
@media screen and (max-width: 375px) {
  .message-header {
    font-size: 26px;
  }
}
.add-faq-btn {
  height: 40px !important;
}

.toggle-menu {
  display: none;
}
.toggle-menu-container {
  width: 100%;
  padding-top: 2rem;
  top: 68px !important;
  background: #fff;
  border-bottom: solid 1px #e6e6e6;
}
.toggle-menu-content {
  list-style: none;
  padding-bottom: 1rem;
  max-width: 300px;
  width: 100%;
  margin: 0 auto;
  padding-left: 0;
}
@media screen and (max-width: 768px) {
  #navItem-right {
    display: none;
  }
  .toggle-menu {
    display: block;
  }
}

.ant-popover-inner-content {
  padding: 0 !important;
}

